* 
	padding: 0
	margin: 0
	border: 0

*, *:before, *:after 
	-moz-box-sizing: border-box
	-webkit-box-sizing: border-box
	box-sizing: border-box

:focus, :active 
	outline: none

a:focus, a:active 
	outline: none

nav, footer, header, aside 
	display: block

html, body 
	height: 100%
	width: 100%
	font-size: 100%
	line-height: 1
	font-size: 14px
	-ms-text-size-adjust: 100%
	-moz-text-size-adjust: 100%
	-webkit-text-size-adjust: 100%

input, button, textarea 
	font-family: inherit

input::-ms-clear 
	display: none

button 
	cursor: pointer
	&::-moz-focus-inner 
		padding: 0
		border: 0
	

a, a:visited 
	text-decoration: none

a:hover 
	text-decoration: none

li
	list-style: none

img 
	vertical-align: top

h1, h2, h3, h4, h5, h6 
	font-size: inherit
	font-weight: 400



body
	background-color: #fff

.container
	max-width: 1230px
	width: 100%
	margin: 0 auto
	padding: 0 15px
.header-line
	position: fixed
	height: 105px
	background: #fff
	width: 100%
	z-index: 999
.header
	&__top
		display: flex
		justify-content: space-between
		align-items: center
		padding: 26px 0 30px 0
		font-family: 'Jost', sans-serif

	&__list
		display: flex
		justify-content: space-between
		
	&__link
		font-weight: 400
		font-size: 19px
		line-height: 30px
		letter-spacing: 0.4px
		color: #292F36
		cursor: pointer
		&:hover
			color: #CDA274
.list__body
	max-width: 538px
	width: 100%
.logo
	max-width: 177px
	height: 100%
	display: block
.header__main
	background-image: url(../images/background.jpg)
	background-size: cover
	background-repeat: no-repeat
	border-radius: 0px 0px 0px 290px
	padding-top: 105px
.header__button
	font-family: 'Jost'
	font-weight: 400
	font-size: 18px
	line-height: 27px
	letter-spacing: 0.72px
	color: #FFFFFF
	background: #292F36
	border-radius: 18px
	padding: 22px 0 22px 34px
	text-align: left
	width: 100%
	position: relative
	&::before
		content: ""
		display: block
		width: 25px
		height: 2px
		background: #CDA287
		position: absolute
		right: 23px
		top: 34.5px
.butoon__body
	position: relative
	max-width: 207px
	margin-top: 35px
.strel
	position: absolute
	top: 16px
	right: 20px
	
.header-title
	font-weight: 400
	font-size: 80px
	line-height: 96px
	letter-spacing: 0.22px
	font-family: 'DM Serif Display', serif
	color: #292F36
.title
	font-family: 'DM Serif Display', serif
	font-weight: 400
	font-size: 50px
	line-height: 65px
	letter-spacing: 0.22px
	color: #292F36
.text
	font-weight: 400
	font-size: 22px
	line-height: 33px
	letter-spacing: 0.22px
	color: #4D5053
	font-family: 'Jost', sans-serif
.main__list
	padding: 188px 0 260px 0

.burger
	display: none
@media(max-width: 1480px)
	.text
		font-size: 18px
		line-height: 25px
	.title
		font-size: 40px
		line-height: 55px
	.header-title
		font-size: 50px
		line-height: 76px
	.header__main
		border-radius: 0px 0px 0px 100px

@media(max-width: 768px)
	.burger-line__active:nth-child(2)
		display: none
	.burger-line__active:nth-child(1)
		margin-top: 7px
		transform: rotate(45deg)
	.burger-line__active:nth-child(3)
		margin-top: -2px
		transform: rotate(-45deg)
	.burger
		display: block
		width: 25px
		height: 19px
		cursor: pointer
		position: fixed
		right: 20px
		top: 30px
	.burger-line
		height: 3px
		width: 25px
		background: #CDA274
		transition: .3s
	.burger-line:nth-child(2)
		margin: 5px 0
	.header
		&__list
			visibility: hidden
			opacity: 0
			text-align: center
			position: fixed
			top: 60px
			background: #fff
			width: 100%
			padding-top: 20px
			height: 320px
			max-width: 768px
			right: 0
			left: 0
			border-radius: 0px 0px 0px 100px
			flex-direction: column
			transition: .3s
		&__list__active
			opacity: 1
			visibility: visible
		&__top
			padding: 16px 0 20px 0
	.logo
		max-width: 130px
	.header-line
		height: 80px
	li
		padding-bottom: 30px
	.header__main
		background-image: url(../images/background-copy.jpg)
